import React, { useState } from 'react'

import { Alert, Box, Button, FormControl, InputAdornment, InputLabel, MenuItem, OutlinedInput, TextField } from '@mui/material'
import axios from 'axios'
import AccessDenied from './AccessDenied'
import Adminheeader from './Adminheeader'

const Addmovie = () => {
    const category=[ {value:"UA"},{value: "A"},{value: "PG"}]
    const timing=[ {value:"7am"},{value: "11am"},{value: "2.30 pm"},{value:"7pm"},{value: "10pm"}]
    const [input, setInputs] = useState({});
    const languages=[{value:"Malayalam"},{value:"Hindi"},{value:"Tamil"},{value:"Telugu"},{value:"English"}]
    const token = useState(sessionStorage.getItem("admintoken"));
    const [email,setemail] = useState(sessionStorage.getItem("email"));
    const [successAlert, setSuccessAlert] = useState(false)
    const [message, setMessage] = useState('');
    // MANAGING INPUTS
    const inputHolder=(e)=>{
        setInputs({...input, [e.target.name]: e.target.value})
        console.log(input);
    }
      //  HANDLING SUBMISION
    const submitHandler=()=>{
     console.log(input)
       if (input.Cast == null || input.Category == null || input.Languages == null ||input.Description == null ||input.Image == null ||input.MovieName == null||input.SeatAvailable == null||input.TicketRates == null ||input.timing == null) 
        {
          setTimeout(() => {
            const messageDisplay = 'Please fill in all the fields.';
            setMessage(messageDisplay);
          }, 1000);
          setTimeout(() => {
            const messageDisplay = '';
            setMessage(messageDisplay);
          }, 5000);
    
        }
        else{
             const seats=Array.from({length:input.SeatAvailable},(_,index)=>({
             seatname:index +1,
             disStatus:false,
              }) )
          // console.log(seats) 
           input.seats=seats;
           console.log(seats)
           axios.post("/api/addMovie/"+token,input)
          .then((response)=>{
          
          console.log(response.data.message)
          setMessage(response.data.message)
          setSuccessAlert(true)
          setTimeout(() => {
           window.location.reload(false)
          }, 1000);
          })
         .catch(error=>console.log(error))
      }
     }
     let renderjsx= <div>
    
     <div className="inside" alignContent="center">
    
     <Adminheeader/>
     
 <Box
       sx={{
       width: 500,
       border:"100px",
       maxWidth: '100%',
       margin:"auto",
       marginTop:"25px"
        }}
     >
       {successAlert?
          <Alert variant="filled" sx={{ margin: "auto", width:"400px", alignContent:"center" }} severity="success">
            {message}
          </Alert>:""}
         <TextField  sx={{backgroundColor:"white"}}fullWidth label="Movie Name" name='MovieName' onChange={inputHolder} id="fullWidth" marginTop='auto'/>
 <Box/>

     <TextField
        onChange={inputHolder}
        name='Category'
       id="outlined-select-currency"
       sx={{marginRight:"10px", width:"160px", backgroundColor:"white", marginTop:"10px"}}
       select
       label="Select Category"
       
       
      >
         {category.map((option) => (
         <MenuItem key={option.value} value={option.value}>
             {option.value}
         </MenuItem>
         ))}
     </TextField>
     <TextField
        onChange={inputHolder}
        name="Languages"
        id="outlined-select-currency"
        select
        label="Select language"
        defaultValue="EUR"
        sx={{marginRight:"10px", width:"150px",backgroundColor:"white", marginTop:"10px"}}
     //    helperText="Please select the language"
     >
        {languages.map((option) => (
         <MenuItem key={option.value} value={option.value}>
           {option.value}
         </MenuItem>
         ))}
   </TextField>
   <TextField
        onChange={inputHolder}
        name="timing"
        id="outlined-select-currency"
        select
        label="Select show time"
        defaultValue="EUR"
        sx={{marginRight:"10px", width:"150px",backgroundColor:"white", marginTop:"10px"}}
     //    helperText="Please select the language"
     >
        {timing.map((option) => (
         <MenuItem key={option.value} value={option.value}>
           {option.value}
         </MenuItem>
         ))}
   </TextField>
 </Box>
 <Box
   sx={{
     width: 500,
     border:"100px",
     maxWidth: '100%',
     margin:"auto",
     marginTop:"10px",
     backgroundColor:"white"
    }}
 >
      <TextField fullWidth label="Cast" name="Cast" onChange={inputHolder} id="fullWidth" marginTop='auto' sx={{backgroundColor:"white"}}/>
 </Box>
 <Box
   sx={{
     width: 500,
     border:"100px",
     maxWidth: '100%',
     margin:"auto",
     backgroundColor:"white",
     marginTop:"10px"
    }}
 >
       <TextField fullWidth label="Story Line"
        onChange={inputHolder}
        name="Description"
         id="fullWidth" 
         multiline
         rows={2}
         marginTop='auto'
       />
 </Box>
 <Box
   sx={{
     width: 500,
     border:"100px",
     maxWidth: '100%',
     margin:"auto",
     marginTop:"10px",
     backgroundColor:"white"
   }}
 >
     <FormControl fullWidth >
       <InputLabel htmlFor="outlined-adornment-amount">Amount</InputLabel>
       <OutlinedInput
       name='TicketRates'
         id="outlined-adornment-amount"
         onChange={inputHolder}
         startAdornment={<InputAdornment position="start"></InputAdornment>}
         label="Amount"
       />
     </FormControl>
 </Box>
 <Box
   sx={{
     width: 500,
     border:"100px",
     maxWidth: '100%',
     margin:"auto",
     marginTop:"10px",
     backgroundColor:"white"
   }}
  >
      <TextField fullWidth label="No Of Seats" name='SeatAvailable'  onChange={inputHolder} id="fullWidth" marginTop='auto'/>
 </Box>
 <Box
   sx={{
     width: 500,
     border:"100px",
     maxWidth: '100%',
     margin:"auto",
     marginTop:"10px",
     backgroundColor:"white"
    }}
 >
       <TextField fullWidth label="Poster image url"
        onChange={inputHolder}
        name='Image'
         id="fullWidth" 
         multiline
         rows={2}
         marginTop='auto'
       />
 </Box>
 <Box ><div style={{ color: 'red', textAlign: "center" }}>{message}</div></Box>
 <Box
 
 sx={{
     width: 500,
     border:"100px",
     maxWidth: '100%',
     margin:"auto",
     marginTop:"10px",
     backgroundColor:"white"
     
   }}>
       <Button
         sx={{ backgroundColor: "rgba(61,180,140, 1)"}} 
         variant='auto' 
         fullWidth 
         textAlign={"center"} 
         size="large"
         onClick={submitHandler}>
          ADD MOVIE
     </Button>
    
</Box>
 </div>
     </div>
     if(!token) renderjsx= <AccessDenied/>; 
  return (
      renderjsx
  )
}

export default Addmovie