import { Typography, Button, Box, ImageList, ImageListItem, ImageListItemBar} from '@mui/material';
import React, { useEffect, useState } from 'react'
import Userheader from './Userheader';
import axios from 'axios';
import { useNavigate } from "react-router-dom"
import AccessDenied from './AccessDenied';

const Userdashboard = () => {
  const[movie,setMovie]=useState([]);
  const [userId,setuserId] = useState(sessionStorage.getItem("userId"));
  const [token,settoken] = useState(sessionStorage.getItem("usertoken"));
  const [email,setemail] = useState(sessionStorage.getItem("email"));
  const navigate=useNavigate()
//  VIEW ALL MOVIES
  useEffect(()=>{
    axios.post("/api/viewMovies")
    .then((response)=>{
        if(response.status===200){
         console.log("success");
         setMovie(response.data.movies);
        }
       else{
         console.log("check the network, status is not ok")
        } 
      })
    .catch((error=>console.log(error)));
   },[])

// SINGLE MOVIE ON CLICK
   const movieHandler=(e)=>{
      const movieId= e._id
      const movieName=e.MovieName
      console.log(movieName);
      sessionStorage.setItem("movieId", movieId);
      sessionStorage.setItem("movieName",movieName);
      const settdata={
        "token": token,
        "email":email,
      }
      console.log(movieId);
      axios.post("/api/viewMovie/"+ movieId,settdata)
      .then((response)=>{
        console.log(response.data)
        if(response.status===200){
          navigate("/movie")
          console.log(response.data)
        }
        else{
          console.log(response.status)
        }
      }) 
      .catch((error=>console.log(error)))
   }
// RENDERING 
    let renderjsx = <div className='body'  style={{ height: "100%" }}>
 <Userheader/>
    <Box className="inside" sx={{ minHeight: "100vh", boxShadow:"5px -0px 6px 10px  grey" }} display={"flex"} >
     
     <ImageList sx={{ margin:"4%",width:1200,height:465,borderRadius:"5px",
        boxShadow:"-10px 0px 10px 10px  white"}}
        gap={10}
        cols={4} 
        rowHeight={"auto"}
        >
          {movie.map((value, index) => (
            <ImageListItem  sx={{border:"solid .5px", borderColor:"wheat"}}>
                <img
               
                  onClick={()=>movieHandler(value)}
                  src={value.Image}
                  alt={value.MovieName}
                  loading="lazy"
                  />
                <Typography  sx={{backgroundColor:"black", color:"white"}}>Category:{value.Category}</Typography>
                <Typography sx={{backgroundColor:"black", color:"white"}}> Language: {value.Languages}</Typography>
                 <ImageListItemBar
                   sx={{
                    background:
                      'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
                      'rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)'
                      }}
                    title=  {value.MovieName}
                    position="top"
                    subtitle={value.Category}
                    actionIcon={
                      <Button
                        size='small'
                        sx={{ color: 'black', backgroundColor:"yellow", opacity:"0.6" }}
                        onClick={()=>movieHandler(value)}
                       >  
                         Book
                      </Button>
                    }
                    actionPosition="right"
                />
                
            </ImageListItem>
          ))}
      </ImageList>
    </Box>
    
    </div>
      if(!token) renderjsx= <AccessDenied/>; 
  return (
    renderjsx
  )
}
export default Userdashboard;