import { Grid, Stack, Card, CardContent, Typography, CardActions, Button, Rating,  Box, IconButton, Tooltip, TextField, MenuItem, FormControl, InputLabel, OutlinedInput, InputAdornment, Dialog, DialogTitle, DialogContent, DialogActions } from '@mui/material';
import React, { useEffect, useState } from 'react'

import axios, { Axios } from 'axios';
import { useNavigate } from "react-router-dom"
import EditNoteIcon from '@mui/icons-material/EditNote';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import { convertLength } from '@mui/material/styles/cssUtils';
import AccessDenied from './AccessDenied';

import Adminheeader from './Adminheeader';


const Admindashboard = () => {
  const [movie, setMovie] = useState([]);
  const [todaysbooking, settodaysbooking] = useState([]);
  const timing = [{ value: "7am" }, { value: "11am" }, { value: "2.30 pm" }, { value: "7pm" }, { value: "10pm" }]
  const [avgRating, setAvgRating] = useState([])
  const [updateid, setid] = useState([])
  const [moviepage, setmoviepage] = useState("true");
  const [open, setOpen] = React.useState(false);
  const [input, setInputs] = useState({});
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'));
  const [token,settoken] = useState(sessionStorage.getItem("admintoken"));
  const [email,setemail] = useState(sessionStorage.getItem("email"));

  const navigate = useNavigate();
  const movieWitAvg = movie.map((obj, index) => ({
    ...obj,
    ...avgRating[index],
  }))
  const inputHolder = (e) => {
    setInputs({ ...input, [e.target.name]: e.target.value })
    console.log(input);
  }
  useEffect(() => {
    axios.post("/api/getavg")
      .then((response) => {
        if (response.status == 200) {
          //   console.log("first")
          setAvgRating(response.data)
         
          axios.post("/api/viewMovies")
            .then((response) => {
              console.log(response.data.movies);
              setMovie(response.data.movies);
              settodaysbooking(response.data)
            })
       
        }
        else {
          console.log("check the network, status is not ok")
        }
      })
      .catch((error => console.log(error)));
  }, [])

  console.log(movieWitAvg)
  //  DELETE MOVIE
  const deleteHandler = (e) => {
    let id = e._id
    console.log(id)
    const auth={"token":token}
    axios.post("/api/deleteMovie/" + id, auth)
      .then((response) => {
        if (response.data.message === "movie removed successfully") {
          console.log(response.data.message);
          alert("deletd succesfully")
          window.location.reload(false);
        }
        else {
          console.log(response.data.message)
        }
      })
      .catch((error => console.log(error)))

  }
  const handleClose = () => {
    setOpen(false);
  };
  // update movie
  const updateHandler = (e) => {
    setOpen(true);
    setid(e._id);
    setmoviepage(e);
    console.log(moviepage)
  }
  //  conform update
 
  const conformUpdate = () => {
    console.log(updateid)
    input.token=token;
    console.log(input)
    setOpen(false);
    axios.post("/api/updateticket/"+ updateid,input)
    .then((response)=>{
      console.log(response.data.message)
      if(response.data.message=== "updated successfully"){
        alert("updated successfully")
        window.location.reload(false);
      }
     
    })
    .catch((error=>console.log(error)))
  
  }
  let renderjsx =<div className='body' style={{ height: "100%", backgroundColor:" #051937"  }}>
 <Adminheeader/>
  <Box className="inside" sx={{ minHeight: "100vh" }} display={"flex"} >
    <Grid container my={1} padding={2} margin={"auto"} sx={{ alignItems: "center", margin: "auto" }} >
      {movieWitAvg.map((value, index) => {
        return <Grid item key={index} xs={12} md={3} sm={6} xl={3} my={2} display={'flex'} >
          <Card height={"100"} margin={"2px"} display={"flex"} sx={{ minWidth: "250px",boxShadow:"-10px 0px 10px 5px white", maxHeight: "500px", minHeight: "150px", width: "", backgroundImage: "linear-gradient(TO left bottom, #051937, #004D7A, #008793,#00BF72,#A8EB12)", ':hover': { boxShadow: 20 } }}>

            <CardContent  >
              <Stack direction={"row"}>
                <img
                  height={"224"}
                  width={"50%"}
                  src={value.Image}
                  alt="movie poster"
                  margin="auto"
                />
                <Stack sx={{ color: "white", marginLeft: "5px", padding: "5px" }}>
                  <Typography gutterBottom variant='h6' component="div">
                    {value.MovieName}
                  </Typography>

                  <><Typography gutterBottom variant='body1' component="div">
                    {value.Category}
                  </Typography>
                    <Typography gutterBottom variant='paragraph' component="div">
                      {value.Languages}
                    </Typography>
                    <Typography gutterBottom variant='paragraph' component="div">
                      Collection: &nbsp;{value.NoOfSeats}
                    </Typography>
                    <Typography variant='paragraph' component="div">
                      RATING: {value.avgRating}
                    </Typography> </>


                </Stack>
              </Stack>
            </CardContent>
            <Rating readOnly precision={0.5} value={value.avgRating} sx={{ marginLeft: "80px" }} />
            <CardActions sx={{ marginLeft: "auto" }} >
              <Tooltip title="edit" placement="top-start" sx={{ margin: "auto" }}
                onClick={() => updateHandler(value)}
              >
                <IconButton>
                  <EditNoteIcon />
                </IconButton >
              </Tooltip>
              <Tooltip title="delete" placement="top-start" sx={{ margin: "auto" }} onClick={() => deleteHandler(value)}>
                <IconButton>
                  <DeleteForeverIcon />
                </IconButton>
              </Tooltip>
            </CardActions>
            <Dialog
              fullScreen={fullScreen}
              onClose={handleClose}
              open={open}
              // onClose={handleClose}
              aria-labelledby="responsive-dialog-title"
            >
              <DialogTitle id="responsive-dialog-title">
                {moviepage.MovieName}
              </DialogTitle>
              <DialogContent>
                <Box
                  sx={{
                    width: 150,
                    border: "250px",
                    maxWidth: '100%',
                    margin: "auto",
                    marginTop: "10px"
                  }}
                >
                  <FormControl fullWidth >
                    <InputLabel htmlFor="outlined-adornment-amount">Amount</InputLabel>
                    <OutlinedInput
                      name='TicketRates'
                      id="outlined-adornment-amount"
                      defaultValue={moviepage.TicketRates}
                      onChange={inputHolder}
                      startAdornment={<InputAdornment position="start"></InputAdornment>}
                      label="Amount"
                    />

                  </FormControl>
                </Box>
                <Box
                  sx={{
                    width: 150,
                    border: "250px",
                    maxWidth: '100%',
                    margin: "auto",
                    marginTop: "10px"
                  }}
                >
                  <TextField
                    onChange={inputHolder}
                    name="timing"
                    // value={value.timing}
                    id="outlined-select-currency"
                    select
                    label="Select show time to update"
                    defaultValue={moviepage.timing}
                    sx={{ marginRight: "10px", width: "150px" }}
                     
                  >
                    {timing.map((option) => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.value}
                      </MenuItem>
                    ))}
                  </TextField>
                </Box>
              </DialogContent>
              <DialogActions>

                <Button onClick={conformUpdate} autoFocus>
                  Update
                </Button>
              </DialogActions>
            </Dialog>
          </Card>
        </Grid>
      })}

    </Grid>
  </Box>
 

  
</div>
if(!token)renderjsx= <AccessDenied/>;

  return (
    renderjsx
  )
}

export default Admindashboard