import React, { useEffect, useState } from 'react'
import{AppBar, Box, Grid, ImageList, Stack, Tab,Tabs, Toolbar, Typography} from '@mui/material'
import TheatersIcon from '@mui/icons-material/Theaters';

const Headder = () => {
  
  const [movie,setMovie]=useState([]);
 

  
  return (
   <AppBar  position='stickey' 
  
  sx={{bgcolor:"#2b2d42", boxShadow:"5px 3px 10px 2px white"}}
   >
     <Toolbar>
       <Box width={'20%'}>
        <Stack direction={"row"}>
          <TheatersIcon/> &nbsp;<Typography>Movie Hive</Typography>
         </Stack>
       </Box>
       
       <Box  display={"flex"} sx={{marginLeft:"auto"}}>
         <Tabs  textColor='white' indicatorColor='white' >
            <Tab  label="LogIn" href='/login' />
            <Tab label="SignUp" href='/signup'/>
         </Tabs>

       </Box>
     </Toolbar>

   </AppBar>
  )
}

export default Headder