import React, { useEffect, useState } from 'react'
import Userheader from './Userheader'
import { Box, Button, Card,  Rating, CardContent, Container, Grid,  Stack, Typography, TextField, IconButton } from '@mui/material'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import Modal from 'react-bootstrap/Modal';
import SendTwoToneIcon from '@mui/icons-material/SendTwoTone';
import RateReviewTwoToneIcon from '@mui/icons-material/RateReviewTwoTone';
import AccessDenied from './AccessDenied'
import CloseIcon from '@mui/icons-material/Close';


const Bookedtickets = () => {
   
    const [bookedMovies, setBookedMovies] = useState([]);
    const [currentdate, setDate] = useState(new Date())
    const [rateingval, setRatingval] = useState({})
    const [Input, setInputs] = useState( "" );
    const [show, setShow] = useState(false);
    const userName =sessionStorage.getItem("userName")
    const [userId,setuserId] = useState(sessionStorage.getItem("userId"));
    const [token,settoken] = useState(sessionStorage.getItem("usertoken"));
    const [email,setemail] = useState(sessionStorage.getItem("email"));
    const [tiketData,setTiketdata]=useState([]);
    const [inptvall,setinptvall]=useState("")
    const [datte,setdatte]=useState()
    currentdate.setHours(0,0,0,0);
    const currentdateUTC=currentdate.toISOString();
    const handleClose = () => setShow(false);
    const navigate = useNavigate()

    const handleShow = (e) =>{
        setShow(true);
        setTiketdata(e.moivieId)
        console.log(tiketData);
    }  
    const handleChange = (e,val) => {
        setRatingval(val)
        console.log(rateingval)
    }
    const inputHolder=(e,val)=>{
        setInputs({...Input, [e.target.name]: e.target.value})
        
        console.log(Input.riviews);
       
    }

    const sendHandler =()=>{
       console.log(Input);
       console.log(rateingval);
       
       
       const reviws={
           userName,
            Input,
            rateingval,
            email,
            token
        }
        axios.post("/api/addreviews/" +tiketData,reviws)
        .then((response)=>{
            console.log(response.data.message)
            if(response.data.message==="Thank you for the review"){
                     alert(response.data.message)
                     window.location.reload(false);
            } 
        })
        .catch((error=> console.log(error)));
    }

    useEffect(() => {
       const auth={"token":token}
        axios.post("/api/getbookedtkts/" + userId,auth)
            .then((response) => {
                console.log(response.data)
                setDate(new Date());
               
                if (response.data == "") {
                    alert("Ooops!!! No  bookings yet, but it's Never Late");
                    navigate("/Userdashboard")
                }
                else {
                    setBookedMovies(response.data);


                }
                console.log(currentdate);

            })

            .catch((error => console.log("error")))
    }, [])

    //  cancelling tickets
    const CancelHandler = (e) => {
        
        const tktData={
            "userId": e.userId,
            "movieName": e.movieName,
            "seatNo": e.seatNo,
            "movieId":e.moivieId,
            "token":token    
        }
        console.log(tktData);
        const tktId = e._id;
        axios.post("/api/cancelticket/" + tktId, tktData)
            .then((response) => {
                console.log(response.data.message)
                if (response.data.message === "Ticket Cancelled") {
                    alert(response.data.message);
                    window.location.reload(false);
                }
                else {
                    console.log("cannot cancel")
                }

            })
            .catch((error => console.log("error")))
    }
    let rrenderjsx=<div className='body' style={{ height: "100%", backgroundImage: "linear-gradient(TO RIGHT TOP, #051937, #004D7A, #008793,#00BF72,#A8EB12)" }}>
     <Userheader />
    <Box className="signup" sx={{ minHeight: "100vh" }} display={"flex"}>
   
        <Container maxWidth="sm" height="100%"
         sx={{ marginTop: "2%", marginBottom: "5%" }}
         >
          
            <Typography variant='h4' align='center'sx={{color:" white", fontFamily:"Roboto"}} marginBottom={1}>
                  Tickets
            </Typography>
            {bookedMovies.map((value, index) => {
                return < Box  className='tickt' padding={0} 
                sx={{ height:"240px", marginTop: "0"}}
                //  backgroundImage: "linear-gradient(TO RIGHT TOP, #051937, #004D7A, #008793,#00BF72,#A8EB12)" }}>
                >
                    <Grid xs={12} sm={12} xl={12}  >
                        {/* <Card  height={'100%'} width={"100%"}  */}
                            {/* sx={{ backgroundColor: "beige", margin: "1px" , marginTop:"2%"}} > */}
                           <Stack direction={"row"} sx={{ height:"180px",border:"solid .5px",  boxShadow:"5px -5px 10px 10px black"}}>
                          <Grid container>
                           <Grid item xs={8} sm={8} xl={8}>
                            <Stack direction={"column"} sx={{marginTop:"1%"}}  display={"flex"}>
                                {/* <CardContent alignContent={"center"}> */}
                                    {/* <div style={{margin:"10px", marginLeft:"2%"}}> */}
                                    <Typography variant='h6' sx={{ marginLeft:"25%", marginTop:"10%"}}>
                                        Movie : {value.movieName}
                                    </Typography>
                                    <Typography variant='h6' sx={{ marginLeft:"25%"}}>
                                        Seats : {value.seatNo}
                                    </Typography>
                                    <Typography  variant='h6' sx={{ marginLeft:"25%"}}>
                                        {/* Booked on: {Date(value.date)} */}
                                        Date : {new Date(value.date).getDate()}-  {new Date(value.date).getMonth()}-  {new Date(value.date).getFullYear()}
                                    </Typography>
                                   </Stack>
                                   </Grid>
                                   <Grid item xs={4} sm={4} xl={4}>
                                   <Stack direction={"column"}>
                                    {currentdate<new Date(value.date)?
                                    <Button
                                       
                                        value={value}
                                        variant='text'
                                        onClick={() => CancelHandler(value)}
                                        sx={{ justifyContent: "center",':hover': { backgroundColor:"black", color:"white"}, boxShadow:"5px 0px 10px 10px #004D7A",marginTop:"40%",backgroundColor:" gold", width:"100px" }} >
                                          CANCEL
                                    </Button>
                                     :   
                                    <>
                                            {/* <h6>RATING : </h6> <Rating value={rateingval} onChange={handleChange} sx={{ color: 'red' }} /> */}
                                    <Button
                                    sx={{marginTop:"40%",backgroundColor:"gold", width:"100px",':hover': { backgroundColor:"black", color:"white"} , boxShadow:"5px -0px 10px 10px #004D7A" }}
                                    
                                     variant="standared"
                                      onClick={()=>handleShow(value)}
                                        startIcon={<RateReviewTwoToneIcon/> }>
                                         Review 
                                    </Button>
                                    <Modal className='signup'  show={show} onHide={handleClose }
                                        style={{ minHeight: "650px", padding: "100px",margin:"25px" }}>
                                        <Grid container style={{ minHeight: "50px",borderRadius:"10px",padding:"25px", boxShadow:"10px -0px 10px 10px black" }}>
                                            <Grid item xs={12} md={12}  >
                                         
                                                <Typography variant='h6' fontFamily={"Roboto"} sx={{marginBottom:"5px"}}>
                                                     How was the movie {value.movieName} ?? Share your thoughts
                                                   
                                                </Typography>
                                                </Grid>
                                                    <Grid item xs={12} md={12}>
                                                   
                                                        <h6  fontFamily={"Roboto"}>Rate the movie  : </h6> 
                                                        <Rating 
                                                            value={rateingval}  
                                                            name="ratings"
                                                            
                                                            onChange={handleChange}
                                                            sx={{ color: 'yellow' }} 
                                                         />
                                                          <Grid item xs={12} md={12} >
                                                            
                                                        <TextField
                                                            autoFocus
                                                            margin="auto"
                                                            name="riviews"
                                                            label="Your review"
                                                            type="text"
                                                            fullWidth
                                                            sx={{marginTop:"10px", marginBottom:"10px", boxShadow:"10px -0px 10px 10px  grey"}}
                                                            variant="outlined"
                                                            onChange={inputHolder}
                                                        />
                                                        </Grid>
                                                       <Grid item xs={12} md={12} >
                                                          <Button variant="text" 
                                                            
                                                            onClick={sendHandler}
                                                            endIcon={<SendTwoToneIcon /> }>
                                                              SUBMIT
                                                         </Button >
                                                           <CloseIcon onClick={handleClose } sx={{position:"absolute", right:"0", top:"10%"}}/>
                                                         </Grid>
                                                    </Grid>
                                                </Grid>
                                            </Modal>
                                        </>
                                     }  
                                {/* </CardContent> */}
                            
                                </Stack>
                                </Grid>
                            </Grid>
                        </Stack>  
                        {/* </Card> */}
                    </Grid>
                </Box>
                //    </Stack>
            })}

        </Container>
    </Box>
</div>
if(!token)rrenderjsx=<AccessDenied/>
    return (
       rrenderjsx
    )
}

export default Bookedtickets