import React, { useState } from 'react'
import './Header.css'
import { useNavigate } from 'react-router-dom';
import TheatersIcon from '@mui/icons-material/Theaters';
import { Avatar, Stack, Typography } from '@mui/material';

const Adminheeader = () => {
   
  const [movie,setMovie]=useState([]);
  const [userName]=useState(sessionStorage.getItem("userName"));
  const [value,setValue]=useState(null)
  const navigate=useNavigate()
     const logout=()=>{
        const items=["adminId","movieId","movieName","admintoken","email" ]
        items.forEach((item)=>{
          sessionStorage.removeItem(item)
        })
       ;
        navigate("/");
    }
  return (
   
    <div className="test" style={{boxShadow:"10px -0px 10px 10px  white"}}>
      <nav className="navbar navbar-expand-lg  navbar-dark">
        <div className="container-fluid">
          <a className="navbar-brand" href="#">  <TheatersIcon/> Movie Hive    </a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
             <span className="navbar-toggler-icon"></span>
           </button>
          <div className="collapse navbar-collapse float" id="navbarNavAltMarkup">
             <div className="navbar-nav">    
               <div className="btn-group">
                 <button type="button" className="btn btn-success" style={{backgroundColor:" #004D7A",boxShadow:"-5px 0px 5px 5px  white"}}>
                  <Stack direction={"row"}>
                   <Avatar 
                      size="xs"
                        src="/broken-image.jpg" />
                      <Typography sx={{marginTop:"6px", marginLeft:"12px"}}>
                      ADMIN
                      </Typography>
                    
                     </Stack>
                     </button>
                 <button type="button" style={{backgroundColor:" #004D7A",boxShadow:"5px 0px 5px 5px  white"}} className="btn btn-success dropdown-toggle dropdown-toggle-split" data-bs-toggle="dropdown" aria-expanded="false">
                  <span className="visually-hidden">Toggle Dropdown</span>
                 </button>
                 <ul className="dropdown-menu position3" style={{position:"absolute", right:2, backgroundColor:" #004D7A",boxShadow:"-5px 1px 5px 5px  white"}}>
                   <li ><a className="dropdown-item text-white custom-hover"href='/addmovie' ><svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="currentColor" class="bi bi-power" viewBox="0 0 16 16" color=" #004D7A"/>
                       Add Movies</a></li>
                    <li ><a className="dropdown-item text-white custom-hover"href='/admindashboard' ><svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="currentColor" class="bi bi-power" viewBox="0 0 16 16" color=" #004D7A">
                    </svg> Movies</a></li>
                   <li ><a className="dropdown-item text-white custom-hover"href="/" onClick={logout}><svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="currentColor" class="bi bi-power" viewBox="0 0 16 16" color=" #004D7A">
                      <path d="M7.5 1v7h1V1h-1z" stroke="#001C30" stroke-width="0.5"/>
                       <path d="M3 8.812a4.999 4.999 0 0 1 2.578-4.375l-.485-.874A6 6 0 1 0 11 3.616l-.501.865A5 5 0 1 1 3 8.812z" stroke="#001C30" stroke-width="0.5"/>
                   </svg> Log Out</a></li>
                 </ul>
  
               </div>
            </div>
        </div>
     </div>
   </nav>
  </div>


    
  )
}

export default Adminheeader