import React, { useEffect, useState } from 'react'

import Headder from './Headder'
import {  Box, Grid, ImageList, ImageListItem, ImageListItemBar, Stack, Typography } from '@mui/material'

import axios from 'axios';



const Home = () => {
  const [movie, setMovie] = useState([]);

  //  
  useEffect(() => {
    axios.post("/api/viewMovies")
      .then((response) => {
        if (response.status == 200) {
          console.log("success");
          setMovie(response.data.movies);
        }
        else {
          console.log("check the network, status is not ok")
        }
      })
      .catch((error => console.log(error)));
  }, [])
  console.log(movie)
  //
  return (
    <div className='body' >
      <Grid xs={12} md={12} lg={12}>
        <Box className="inside" sx={{  width:"auto", height: "600px" }}>
          <Headder />
          <Grid container>
            <Grid item xs={12} sm={12} md={6} lg={6}>

              <Typography variant='h3' sx={{ color: "white", marginLeft: "10px", marginTop: "150px" }}>
                "Unleash the emotion, with every ticket"
              </Typography>
            </Grid>

            <Grid item xs={12} sm={12} md={6} lg={6}>
              
              <ImageList
                sx={{ width: "auto", height: "480px", marginTop: "25px",borderRadius:"5px",
                  marginBottom: "auto",position:"absolute", 
                  boxShadow:"-10px 0px 10px 10px  rgba(0,0,0,8)",borderColor:"whitesmoke",
                 marginRight:"auto", backgroundColor:"background-color: hsl(221, 35%, 52%)"}}
                  cols={3} rowHeight={"auto"}>
                {/* <Typography margin={"auto"}>
                  Go through our movies
                  </Typography> */}
                {movie.map((value, index) => (
                  <ImageListItem >
                    <img
                      src={value.Image}
                      alt={value.MovieName}
                      loading="lazy"
                    />
                    <ImageListItemBar
                      title={value.MovieName}
                      subtitle={value.Cast}
                     />
                 </ImageListItem>
                ))}
              </ImageList>
            </Grid>
          </Grid>
        </Box>
      </Grid>


    </div>
  )
}

export default Home