import React, { useState } from 'react'
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import {  useNavigate } from "react-router-dom"
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { Box, Typography, Alert, Tooltip, IconButton } from '@mui/material';
import axios from 'axios';
import AddTaskIcon from '@mui/icons-material/AddTask';

const Signup = () => {
  const navigate = useNavigate()
  const [input, setInput] = useState([]);

  // front end validation
  const emailRegex = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/;
  const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{6,}$/;
  const [emailError, setEmailError] = useState('');
  const [passwordError, setpasswordError] = useState('');
  const [message, setMessage] = useState('');
  const [successAlert, setSuccessAlert] = useState(false)
  const [erralert, seterralert] = useState(false)
  const [messageFromBackend, setmessageFromBackend] = useState('');

  // INPUT HANDLER
  const inputHolder = (e) => {
    setInput({ ...input, [e.target.name]: e.target.value })
    console.log(input);
  }

  // SIGN UP FORM HANDLER
  const signupHandler = () => {
    // Front End Form Validation ;
    console.log("clicked", input)
    // empty inputs
    if (input.Name == null || input.email == null || input.password == null) {
      setTimeout(() => {
        const messageDisplay = 'Please fill in all the fields.';
        setMessage(messageDisplay);
      }, 1000);
      setTimeout(() => {
        const messageDisplay = '';
        setMessage(messageDisplay);
      }, 5000);

    }
    // email id Checking
    else if (!emailRegex.test(input.email)) {
      setEmailError('Please enter a valid email address.');
      setTimeout(() => {
        setEmailError("");
      }, 5000);
    }

    //   Password Checking
    else if (!passwordRegex.test(input.password)) {
      setpasswordError('Password should  be atleast 6 characters with an uppercase, a lower case, a letter, a digit and a special character  ');
      setTimeout(() => {
        setpasswordError("");
      }, 5000);

    }
    

    else {
      axios.post("/api/signup", input)
      .then((response) => {
        console.log(response.data)
        if (response.data.message === "Registered succesfully") {
          setmessageFromBackend(response.data.message);
          setSuccessAlert(true);
          setTimeout(() => {
            navigate("/login");
          }, 2000);

        }
        else {
          seterralert(true)
          setmessageFromBackend(response.data.message);

          setTimeout(() => {
            window.location.reload(false) ;  
            }, 2500)
        }
      })
      .catch((error) => {
        console.log(error);
      })
    }
  }
  return (
    <div>
      <Dialog open={"true"}  >
        <Box className="signup">
        <Box margin={"1%"}>
          <Typography
            fontSize="30px"
            color={"blue"}
            paddingTop={2}
            lineHeight={1.5}
            fontWeight="500"
            textAlign={"center"}
            fontFamily={"Roboto"}
          >
            Sign Up
          </Typography>
        </Box>
        <DialogContent >
          <TextField
            // autoFocus
            margin="auto"
            name="Name"
            label="Name"
            type="text"
            sx={{backgroundColor:"white", marginTop: "5%",marginLeft:"1%",boxShadow: "5px 5px 10px 2px rgba(0,0,0,8)"}}
            fullWidth
            variant="outlined"
            onChange={inputHolder}
          />
          <TextField
            // autoFocus
            margin="auto"
            name="email"
            label="Email Address"
            type="email"
            fullWidth
            variant="outlined"
            sx={{backgroundColor:"white", marginTop: "5%",marginLeft:"1%",boxShadow: "5px 5px 10px 2px rgba(0,0,0,8)"}}
            onChange={inputHolder}
          />
          <div style={{ color: 'red' }}>{emailError}</div>
          <span> </span>
          <TextField
            // autoFocus
            margin="auto"
            name="password"
            label="Password"
            type="password"
            fullWidth
            variant="outlined"
            sx={{backgroundColor:"white", marginTop: "5%",marginLeft:"1%",boxShadow: "5px 5px 10px 2px rgba(0,0,0,8)"}}
            onChange={inputHolder}
          />
          <div style={{ color: 'red' }}>{passwordError}</div>
        </DialogContent>
        <Box ><div style={{ color: 'red', textAlign: "center" }}>{message}</div></Box>
        {successAlert?
        <Alert variant="filled" sx={{ margin: "20px", width: "400px", alignContent: "center" }} severity="success">
          {messageFromBackend}
        </Alert>:erralert?
        <Alert variant="filled" sx={{ margin: "20px" }} severity="warning">
            {messageFromBackend} <strong>check it out!</strong>
        </Alert>:""}
        <DialogActions >
       

          <Button
            sx={{ margin: "10px" }}
            variant='auto'
            fullWidth
             color='white'
            textAlign={"center"}
            size="large"
            onClick={signupHandler}
          
              startIcon={<Tooltip title="Sign Up"><IconButton sx={{ backgroundColor: "beige", marginLeft: "20px" }}>< AddTaskIcon /></IconButton></Tooltip>}>
             <Typography color={"white"}> SIGN UP</Typography>
          </Button>
        </DialogActions>
        <Box margin={"auto"} >
          <Typography
            marginBottom={"10%"}
            color={"white"}
            fontSize="15px"
            paddingTop={2}
            lineHeight={1.3}
            fontWeight="500"
            textAlign={"center"}
          >
            <i> Already have an account? <a href="/login">Log In</a></i>
          </Typography>
        </Box>
        </Box>
      </Dialog>
    </div>
  )
}

export default Signup