import React, { useState } from 'react'
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';

import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { Box, Typography, Link, Alert, AlertTitle, Stack, IconButton, Tooltip } from '@mui/material';
import axios from 'axios';
import { useNavigate } from "react-router-dom"
import ExitToAppTwoToneIcon from '@mui/icons-material/ExitToAppTwoTone';
const Login = () => {
  const navigate = useNavigate()
  const [input, setInputs] = useState({});
  const [message, setMessage] = useState('');
  const [alertt, setalertt] = useState(false)
  const [erralert, seterralert] = useState(false)
  const [successAlert, setSuccessAlert] = useState(false)
  const inputHolder = (e) => {
    setInputs({ ...input, [e.target.name]: e.target.value })
    console.log(input);
  }
  

  const loginHandler = () => {
    if (input.email == null || input.password == null) {
      setalertt(true)
      setTimeout(() => {
        const messageFromBackend = 'Please fill in all the fields.';
        setMessage(messageFromBackend);
      }, 500);
      setTimeout(() => {
        window.location.reload(false);
      }, 5000);
    } else {
      axios.post("/api/login", input)
        .then((response) => {
          console.log(response.data)
          if (response.data.message === "login successfull") {
            const userId = response.data.data._id;
            const userName = response.data.data.Name;
            const email = response.data.data.email;
            const token = response.data.token;
            sessionStorage.setItem("userId", userId);
            sessionStorage.setItem("userName", userName);
            sessionStorage.setItem("email", email);
            sessionStorage.setItem("usertoken", token);
            console.log(userId)
            console.log(userName)
            setMessage(response.data.message)
            setSuccessAlert(true)
            setTimeout(() => {
              navigate("/userdashboard");
            }, 1000);


          }
          else if (response.data.message === "admin login  successfull") {
            // alert("admin login succesfull")
            const adminId = response.data.data._id;
            const email = response.data.data.email;
            const token = response.data.token;
            sessionStorage.setItem("email", email);
            sessionStorage.setItem("adminId", adminId);
            sessionStorage.setItem("admintoken", token);
            console.log(token)

            setMessage(response.data.message)
            setSuccessAlert(true)
            setTimeout(() => {
              navigate("/admindashboard");
            }, 1000);
          }
          else {
            console.log(response.data.message)
            seterralert(true)
            setTimeout(() => {
              const messageFromBackend = response.data.message;
              setMessage(messageFromBackend);
            }, 500);
            setTimeout(() => {
              window.location.reload(false);
            }, 5000);
          }
        })
        .catch(error => console.log(error))
    }
  }
  return (
    <div >

      <Dialog open={"true"}  >
        <Box className="signup">
          <Box margin={"1%"}  >

            <Typography
              sx
              fontSize="30px"
              paddingTop={5}
              lineHeight={1.3}
              fontWeight="500"
              color={"BLUE"}
              fontFamily={"Segoe UI Symbol"}
              textAlign={"center"}
            >
              Log In
            </Typography>


          </Box>

          <DialogContent className='"login' margin="1%" >

            <TextField
              name="email"
              label="Email Address"
              type="email"
              fullWidth
              variant="outlined"
              sx={{
                margin: "auto", maxWidth: "800px", marginTop: "5%",marginLeft:"1%",marginRight:"10%",                minWidth: "300px", boxShadow: "5px 5px 10px 2px rgba(0,0,0,8)",
                backgroundColor: "white"
              }}
              onChange={inputHolder}
            />
            <span marginTop={"10px"}> </span>
            <TextField
              name="password"
              label="Password"
              type="password"
              fullWidth
              variant="outlined"
              sx={{  maxWidth: "800px", marginTop:"5%",marginLeft:"1%",marginRight:"10%", border: "snow", boxShadow: "5px 5px 10px 2px rgba(0,0,0,8)", backgroundColor: "white" }}
              onChange={inputHolder}
            />

          </DialogContent>


          <DialogActions sx={{ alignContent: "center" }}>

            <Button variant="text" onClick={loginHandler}
             sx={{ position: "absolute", bottom: 10, right: 0, left: 0, margin: "20px", marginTop: "45px" }}
              startIcon={<Tooltip title="Log In"><IconButton sx={{ backgroundColor: "beige", marginLeft: "20px" }}><ExitToAppTwoToneIcon /></IconButton></Tooltip>}>
              login
            </Button>


          </DialogActions>
          {successAlert ?
            <Alert variant="filled" sx={{ margin: "20px", width: "400px", alignContent: "center" }} severity="success">
              {message}
            </Alert> :
            <Box>
              {alertt ?
                <Alert variant="filled" sx={{ margin: "10px" }} severity="warning">

                  <strong>check it out!</strong>  {message}
                </Alert> : (erralert ? <Alert variant="filled" sx={{ margin: "20px" }} severity="error">

                  {message} <strong>check it out!</strong>
                </Alert> : "")}

              {/* <div style={{ color: "red" , textAlign:"center"}}>{message}</div> */}
              <Typography
                marginBottom={"2%"}
                marginTop={"2%"}
                color={"wheat"}
                fontSize="15px"
                paddingTop={2}
                lineHeight={1.3}
                fontWeight="500"
                textAlign={"center"}
              >
                <i>Dont have an account yet !!? <a href='/signup' style={{color:"white"}}>Sign Up</a></i>

              </Typography>

            </Box>}
        </Box>
      </Dialog>


    </div>
  )
}

export default Login